// Imports
// =-=-=-=-=-=-=-=-=-=-=--=-=-=-=

@font-face {
    font-family: 'JosefinSans-VariableFont_wght';
    src:  url('./fonts/Josefin_Sans/josefinsans-variablefont_wght-webfont.woff') format('woff')
}

@font-face {
    font-family: 'Questrial';
    src:  url('./fonts/Questrial/questrial-regular-webfont.woff2') format('woff2')
}


// =-=-=-=-=-=-=-=-=-=-=--=-=-=-=


// Variables
// =-=-=-=-=-=-=-=-=-=-=--=-=-=-=

$background: #3e5e7c;
$primary: #ffffff;

// =-=-=-=-=-=-=-=-=-=-=--=-=-=-=


// Overwrite default bootstrap
// =-=-=-=-=-=-=-=-=-=-=--=-=-=-=
.force-background{
    background-color: $background!important;
}
// =-=-=-=-=-=-=-=-=-=-=--=-=-=-=

.app{
    font-family: Questrial;
}


.banner{
    text-align: left;
    padding: 35px;
    background-color: $background;
    color: $primary;
    margin: 0px;
}

.content{
    margin: 0px;
    padding: 0px;
    width: 100vw;
}

.sidebar{
    background-color: $background;
    height: 100vh;
    position: fixed;
    left: 0px;
    transition: width 0.2s ease-in-out;
    width: 120px;
    // color: $primary;
    text-align: left;
    // padding: 10px;

}

.sidebar:hover{
    transition:width 0.2s ease-in-out;
    width: 240px;
}


.custom-nav-item{
    // padding: 10px;
    padding: 10px;
    padding-left: 50px;
    color: $primary;
    font-size: 18px;
    vertical-align: middle;

}

.custom-nav-item:hover{
    background-color: $primary;
    color: $background;
    cursor: pointer;
}


hr {
    color: $primary!important;
    background-color: $primary;
}

.current-title{
    // position: fixed;
    color: $primary;
    background-color: $background;
    right:45vw;
    top: 0px;
    padding: 30px;
    text-align: center;
    width: 100vw;
}

// .title-wrapper{
//     text-align: center;
// }


#sidebar-logo{
    width: 25px;
}

.sidebar-icon{
    font-size: 25px;
}

.underlined{
    border-bottom: 1px solid $background;
}

.nav-separator{
    border-bottom: 1px solid $primary;
    width: 70%;
    margin-left: 15%;
    margin-right: 15%;
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px;
}

.hidden{
    display: none;
}

.active-nav {
    color: $background;
    background-color: $primary;
}

.custom-mobile-nav-item{
    padding:12px;
}

.mobile-menu-wrapper{
    background-color: $background;
    color: $primary!important;
}

.force-foreground{
    color: $primary;
}